<template>
  <div>
    <div class="row justify-content-center">
      <div class="col-md-6 shadow-lg rounded-lg">
        
      
    <!-- <button type="button" @click="test" class="btn btn-sm btn-primary  ">test</button> -->
    <p class="text-center font-bold text-sm p-3 uppercase">
      Aplikasi Sederhana Laporan Harian :
    </p>
    <div class="text-center p-3">
      <img src="@/static/kopdiskominfo.png" class="kinoLightBox img-fluid " style="">
      
    </div>
    <hr class="style16" />
    <div class="p-2">
      <form action="#" @submit.prevent="simpan()">
        <p class="p-2">SKPD :</p>
        <select class="form-control" v-model="vdata.skpd" required disabled>
          <option
            v-for="(item, index) in skpds"
            :key="index"
            :value="item.nama_skpd"
            style="font-size: 12px"
          >
            {{ item.nama_skpd }}
          </option>
        </select>
        <div class="sm-form">
          <label for="tanggal">Tanggal : </label>
          <input
            required
            type="date"
            id="tanggal"
            name="tanggal"
            class="form-control form-control-sm"
            placeholder="tanggal"
            v-model="vdata.tanggal"
          />
        </div>
        <div class="sm-form">
          <label for="keterangan">Keterangan Pekerjaan : </label>
          <div class="sm-form">
            <textarea
              type="text"
              id="keterangan"
              name="keterangan"
              rows="2"
              placeholder="keterangan..."
              class="form-control md-textarea"
              v-model="vdata.keterangan"
              required
            ></textarea>
          </div>
        </div>
        <div class="sm-form">
          <label for="mulai">Mulai Pengerjaan : </label>
          <input
            required
            type="time"
            id="mulai"
            name="mulai"
            class="form-control form-control-sm"
            placeholder="mulai"
            v-model="vdata.mulai"
          />
        </div>
        <div class="sm-form">
          <label for="akhir">Akhir Pengerjaan : </label>
          <input
            required
            type="time"
            id="akhir"
            name="akhir"
            class="form-control form-control-sm"
            placeholder="akhir"
            v-model="vdata.akhir"
          />
        </div>
        <hr class="p-3" />
        <div v-if="aksi == 'insert'" class="text-center">
          <button type="submit" class="btn btn-sm btn-dark">Simpan</button>
        </div>
        <div v-else class="text-center">
          <button type="button" @click="updates" class="btn btn-sm btn-success">
            Update
          </button>
          <button
            type="button"
            @click="deletes"
            class="btn btn-sm btn-danger ml-4"
          >
            Delete
          </button>
          <br />
          <button
            type="button"
            class="btn btn-sm btn-dark"
            @click="
              aksi = 'insert';
              vdata = {};
              getSkpd();
            "
          >
            +
          </button>
        </div>
      </form>
      <Btables
      ref="tablenya"
        :tables="'laporan_bulanan'"
        :hide="['skpd', 'tgl', 'hari', 'tanggal','id']"
        @selected="ambil"
      />
      </div>
    </div>
    </div>
  </div>
</template>
<script>
import autophp from "@/plugins/autophp.js";
let sdb = new autophp();
var id = require("date-fns/locale/id");
import Btables from "@/components/table-laporan.vue";
export default {
  components: {
    Btables,
  },
  data() {
    return {
      skpds: [],
      aksi: "insert",
      vdata: {},
    };
  },
  methods: {
    ambil(data) {
      console.log("ambil");
      this.vdata = JSON.parse(JSON.stringify(data));
      this.aksi = "update";
      this.$forceUpdate();
    },
    format(data) {
      return this.$datefns.format(data, "DD MMMM YYYY", {
        locale: id,
      });
    },
    tgl(data) {
      return this.$datefns.format(data, "DD", {
        locale: id,
      });
    },
    hari(data) {
      return this.$datefns.format(data, "dddd", {
        locale: id,
      });
    },
    simpan() {
      let that=this;
      this.vdata.id_user = this.$store.state.users.id;
      this.vdata.nip = this.$store.state.users.nip;
      this.vdata.nama = this.$store.state.users.nama;
      this.vdata.skpd_id = this.$store.state.users.skpd;
      this.vdata.tanggal_lengkap = this.format(this.vdata.tanggal);
      this.vdata.hari = this.hari(this.vdata.tanggal);
      this.vdata.tgl = this.tgl(this.vdata.tanggal);
      console.log(this.vdata);
      console.log("baru");
      sdb
        .collection("laporan_bulanan")
        .doc()
        .set(this.vdata)
        .then((res) => {
          alert("Berhasil disimpan");
          that.$refs.tablenya.getData();
        });
    },
    updates() {
      let that=this;
      if(confirm('Apakah yakin mengedit data ?')){

        this.vdata.id_user = this.$store.state.users.id;
      this.vdata.nip = this.$store.state.users.nip;
      this.vdata.nama = this.$store.state.users.nama;
      this.vdata.skpd_id = this.$store.state.users.skpd;
      this.vdata.tanggal_lengkap = this.format(this.vdata.tanggal);
      this.vdata.hari = this.hari(this.vdata.tanggal);
      this.vdata.tgl = this.tgl(this.vdata.tanggal);
      console.log(this.vdata);
      console.log("baru");
      sdb
        .collection("laporan_bulanan")
        .doc()
        .set(this.vdata)
        .then((res) => {
          alert("Berhasil Update!");
          that.$refs.tablenya.getData();
        });
      }
    },
    deletes() {
      let that=this;
      if(confirm('apakah yakin menghapus data ?')){

        sdb
        .collection("laporan_bulanan")
        .doc(this.vdata.id)
        .delete()
        .then((res) => {
          console.log(res);
          that.$refs.tablenya.getData();
          alert('berhasil deletes')
        });
          }
    },
    test() {
      let tanggal = new Date();
      console.log(this.format(tanggal));
    },
    getSkpd() {
      let that = this;
      sdb
        .collection("skpd")
        .doc()
        .get("id_skpd,nama_skpd", "order by id")
        .then((res2) => {
          console.log(that.$store.state.users);
          that.skpds = res2;
          let hasil = that.skpds.filter((e) => {
            if (e.id_skpd == that.$store.state.users.skpd) {
              return e;
            }
          });
          that.vdata.skpd = hasil[0].nama_skpd;
          that.$forceUpdate();
        });
    },
  },
  layout: "kominfo",
  mounted() {
    let that = this;
    that.$refs.tablenya.getData();
    sdb
      .collection()
      .ceklogin(this.$store)
      .then((res) => {
        setTimeout(() => {
          sdb
            .collection("skpd")
            .doc()
            .get("id_skpd,nama_skpd", "order by id")
            .then((res2) => {
              console.log(that.$store.state.users);
              that.skpds = res2;
              let hasil = that.skpds.filter((e) => {
                if (e.id_skpd == that.$store.state.users.skpd) {
                  return e;
                }
              });
              that.vdata.skpd = hasil[0].nama_skpd;
              that.$forceUpdate();
            });
        }, 1500);
      })
      .catch((err) => {
        this.$router.push("/auth/login");
      });
  },
};
</script>